(function ($) {
  'use strict';

  $.extend(true, $.trumbowyg, {
      langs: {
          // jshint camelcase:false
          en: {
              fontsize: 'Font size',
              fontsizes: {
                  'x-small': 'Extra small',
                  'small': 'Small',
                  'medium': 'Regular',
                  'large': 'Large',
                  'x-large': 'Extra large',
                  'custom': 'Custom'
              },
              fontCustomSize: {
                  title: 'Custom Font Size',
                  label: 'Font Size',
                  value: '48px'
              }
          },
          da: {
              fontsize: 'Skriftstrrelse',
              fontsizes: {
                  'x-small': 'Ekstra lille',
                  'small': 'Lille',
                  'medium': 'Normal',
                  'large': 'Stor',
                  'x-large': 'Ekstra stor',
                  'custom': 'Brugerdefineret'
              }
          },
          de: {
              fontsize: 'Schriftgre',
              fontsizes: {
                  'x-small': 'Sehr klein',
                  'small': 'Klein',
                  'medium': 'Normal',
                  'large': 'Gro',
                  'x-large': 'Sehr gro',
                  'custom': 'Benutzerdefiniert'
              },
              fontCustomSize: {
                  title: 'Benutzerdefinierte Schriftgre',
                  label: 'Schriftgre',
                  value: '48px'
              }
          },
          es: {
              fontsize: 'Tamao de Fuente',
              fontsizes: {
                  'x-small': 'Extra pequea',
                  'small': 'Peguea',
                  'medium': 'Regular',
                  'large': 'Grande',
                  'x-large': 'Extra Grande',
                  'custom': 'Customizada'
              },
              fontCustomSize: {
                  title: 'Tamao de Fuente Customizada',
                  label: 'Tamao de Fuente',
                  value: '48px'
              }
          },
          fr: {
              fontsize: 'Taille de la police',
              fontsizes: {
                  'x-small': 'Trs petit',
                  'small': 'Petit',
                  'medium': 'Normal',
                  'large': 'Grand',
                  'x-large': 'Trs grand',
                  'custom': 'Taille personnalise'
              },
              fontCustomSize: {
                  title: 'Taille de police personnalise',
                  label: 'Taille de la police',
                  value: '48px'
              }
          },
          hu: {
              fontsize: 'Bet mret',
              fontsizes: {
                  'x-small': 'Extra kicsi',
                  'small': 'Kicsi',
                  'medium': 'Normlis',
                  'large': 'Nagy',
                  'x-large': 'Extra nagy',
                  'custom': 'Egyedi'
              },
              fontCustomSize: {
                  title: 'Egyedi bet mret',
                  label: 'Bet mret',
                  value: '48px'
              }
          },
          it: {
              fontsize: 'Dimensioni del testo',
              fontsizes: {
                  'x-small': 'Molto piccolo',
                  'small': 'piccolo',
                  'regular': 'normale',
                  'large': 'grande',
                  'x-large': 'Molto grande',
                  'custom': 'Personalizzato'
              },
              fontCustomSize: {
                  title: 'Dimensioni del testo personalizzato',
                  label: 'Dimensioni del testo',
                  value: '48px'
              }
          },
          ko: {
              fontsize: ' ',
              fontsizes: {
                  'x-small': ' ',
                  'small': '',
                  'medium': '',
                  'large': '',
                  'x-large': ' ',
                  'custom': ' '
              },
              fontCustomSize: {
                  title: '   ',
                  label: ' ',
                  value: '48px'
              }
          },
          nl: {
              fontsize: 'Lettergrootte',
              fontsizes: {
                  'x-small': 'Extra klein',
                  'small': 'Klein',
                  'medium': 'Normaal',
                  'large': 'Groot',
                  'x-large': 'Extra groot',
                  'custom': 'Tilpasset'
              }
          },
          pt_br: {
              fontsize: 'Tamanho da fonte',
              fontsizes: {
                  'x-small': 'Extra pequeno',
                  'small': 'Pequeno',
                  'regular': 'Mdio',
                  'large': 'Grande',
                  'x-large': 'Extra grande',
                  'custom': 'Personalizado'
              },
              fontCustomSize: {
                  title: 'Tamanho de Fonte Personalizado',
                  label: 'Tamanho de Fonte',
                  value: '48px'
              }
          },
          tr: {
              fontsize: 'Yaz Boyutu',
              fontsizes: {
                  'x-small': 'ok Kk',
                  'small': 'Kk',
                  'medium': 'Normal',
                  'large': 'Byk',
                  'x-large': 'ok Byk',
                  'custom': 'Grenek'
              }
          },
          zh_tw: {
              fontsize: '',
              fontsizes: {
                  'x-small': '',
                  'small': '',
                  'medium': '',
                  'large': '',
                  'x-large': '',
                  'custom': '',
              },
              fontCustomSize: {
                  title: '',
                  label: '',
                  value: '48px'
              }
          },
      }
  });
  // jshint camelcase:true

  var defaultOptions = {
      sizeList: [
          'x-small',
          'small',
          'medium',
          'large',
          'x-large'
      ],
      allowCustomSize: true
  };

  // Add dropdown with font sizes
  $.extend(true, $.trumbowyg, {
      plugins: {
          fontsize: {
              init: function (trumbowyg) {
                  trumbowyg.o.plugins.fontsize = $.extend({},
                    defaultOptions,
                    trumbowyg.o.plugins.fontsize || {}
                  );

                  trumbowyg.addBtnDef('fontsize', {
                      dropdown: buildDropdown(trumbowyg)
                  });
              }
          }
      }
  });

  function setFontSize(trumbowyg, size) {
      trumbowyg.$ed.focus();
      trumbowyg.saveRange();

      // Temporary size
      trumbowyg.execCmd('fontSize', '1');

      // Find <font> elements that were added and change to <span> with chosen size
      trumbowyg.$ed.find('font[size="1"]').replaceWith(function() {
          return $('<span/>', {
              css: { 'font-size': size },
              html: this.innerHTML,
          });
      });

      // Remove and leftover <span> elements
      $(trumbowyg.range.startContainer.parentElement).find('span[style=""]').contents().unwrap();

      trumbowyg.restoreRange();

      trumbowyg.syncCode()
      trumbowyg.$c.trigger('tbwchange');
  }

  function buildDropdown(trumbowyg) {
      var dropdown = [];

      $.each(trumbowyg.o.plugins.fontsize.sizeList, function (index, size) {
          trumbowyg.addBtnDef('fontsize_' + size, {
              text: '<span style="font-size: ' + size + ';">' + (trumbowyg.lang.fontsizes[size] || size) + '</span>',
              hasIcon: false,
              fn: function () {
                  setFontSize(trumbowyg, size);
              }
          });
          dropdown.push('fontsize_' + size);
      });

      if (trumbowyg.o.plugins.fontsize.allowCustomSize) {
          var customSizeButtonName = 'fontsize_custom';
          var customSizeBtnDef = {
              fn: function () {
                  trumbowyg.openModalInsert(trumbowyg.lang.fontCustomSize.title,
                      {
                          size: {
                              label: trumbowyg.lang.fontCustomSize.label,
                              value: trumbowyg.lang.fontCustomSize.value
                          }
                      },
                      function (form) {
                          setFontSize(trumbowyg, form.size);
                          return true;
                      }
                  );
              },
              text: '<span style="font-size: medium;">' + trumbowyg.lang.fontsizes.custom + '</span>',
              hasIcon: false
          };
          trumbowyg.addBtnDef(customSizeButtonName, customSizeBtnDef);
          dropdown.push(customSizeButtonName);
      }

      return dropdown;
  }
})(jQuery);



